import React from "react";
import { Link } from "gatsby";
import { Card } from "react-bootstrap";
// import ImageFallback from "../../components/common/image-fallback";
import { StaticImage, GatsbyImage, getImage } from "gatsby-plugin-image";

// import img from "../../images/blog/unraveling-defi.png"

const BlogsData = (data) => {
  const { data: propsData } = data;
  console.log("propsData::", propsData);
  const blogsData = propsData.edges;
  blogsData.sort((a, b) => b.node.keyId - a.node.keyId);
  console.log("blogsData::", blogsData);

  return (
    <section className="section-develop" id="ourteam">
      <div className="container grid"></div>
      <div className="container">
        <div className="row pb-5 mb-5">
          {blogsData?.map((ele, ind) => {
            const { node } = ele;
            console.log("node img::", node.image);

            return (
              <div className="col-md-4 col-sm-6 ">
                <Card
                  key={ind}
                  className="developer card border rounded shadow mb-4"
                >
                  <Link to={`${node.slug}`} className="text-decoration-none">
                    {/* once image add in src/images/img-src after add to src. for time being and check the below the condition */}
                    {node?.slug === "unraveling-defi" && (
                      <StaticImage
                        src={"../../images/blog-1.png"}
                        alt={"unraveling-defi"}
                      />
                    )}
                    {node?.slug === "crypto" && (
                      <StaticImage
                        src={"../../images/blog-2.png"}
                        alt={"crypto"}
                      />
                    )}
                    {node?.slug === "blockchain" && (
                      <StaticImage
                        src={"../../images/blog-3.png"}
                        alt={"blockchain"}
                      />
                    )}
                    {node?.slug === "E-commerce" && (
                      <StaticImage
                        src={"../../images/blog-4.png"}
                        alt={"E-commerce"}
                      />
                    )}
                    {node?.slug === "Pharma" && (
                      <StaticImage
                        src={"../../images/blog-5.png"}
                        alt={"Pharma"}
                      />
                    )}
                    {node?.slug === "software-products" && (
                      <StaticImage
                        src={"../../images/blog-6.png"}
                        alt={"software-products"}
                      />
                    )}
                    {node?.slug === "crypto-trading-bot" && (
                      <StaticImage
                        src={"../../images/blog-7.png"}
                        alt={"crypto-trading-bot"}
                      />
                    )}
                  </Link>
                  <Card.Body>
                    <div className="developer-inner">
                      <div className="d-flex justify-content-between align-items-center">
                        <Link
                          to={`${node.slug}`}
                          className="text-decoration-none text-muted"
                        >
                          <h4 className="mb-4 text-dark fs-semibold pt-2 lh-sm">
                            {node.title.slice(0, 40)}
                            {"..."}
                          </h4>
                        </Link>
                      </div>
                      <p className="cta-text mb-2 pt-2">
                        {node?.short_description.slice(0, 85)}
                        <Link to={`${node.slug}`}>
                          ...<br></br>Read More
                        </Link>
                      </p>
                      <div className="d-flex align-items-center justify-content-between pt-3">
                        <p className="fs-4">{node.blog_created_date}</p>
                        <p>Site Admin</p>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default BlogsData;
